"use client"

import React from "react"
import { useSearchParams } from "next/navigation"
import { Button } from "@/components/ui/button"

export default function ConsolePage() {
  const searchParams = useSearchParams()
  const agentId = searchParams.get("agent")

  function handleClose() {
    window.close()
  }

  return (
    <div className="max-w-4xl mx-auto mt-10 p-6 border rounded-md shadow-md">
      <h1 className="text-2xl font-semibold mb-4">WebSocket Console</h1>
      <p className="mb-6">
        Connected to agent: <strong>{agentId || "Unknown"}</strong>
      </p>
      <div className="border rounded p-4 h-64 overflow-auto bg-black text-green-400 font-mono">
        {/* WebSocket console output will appear here */}
        <p>Console output will be displayed here.</p>
      </div>
      <form className="mt-4 flex space-x-2">
        <input
          type="text"
          placeholder="Enter command"
          className="flex-grow rounded border px-3 py-2"
          disabled
        />
        <Button disabled>Send</Button>
      </form>
      <Button variant="destructive" className="mt-4" onClick={handleClose}>
        Close Connection
      </Button>
      <p className="mt-4 text-sm text-gray-500">
        WebSocket functionality to be implemented.
      </p>
    </div>
  )
}
